export default {
	data() {
		return {
			percentage: '',
			collection: false,
			example: [],
			index: 0,

			interpretation: [],
			questionList: [],
			exampleArr: [],
			wordId: "",
			doneCount: "",
			wordIdListStr: [],
			time: '',
			Hours: 0,
			Minute: 0,
			Second: 0,
			wordIdList: [],
			rightNum: 0,
			allWordCount:0
		}
	},
	// 页面卸载
	onHide: function() {
		console.log('App Hide')
		clearInterval(this.timer)
	},
	methods: {
		// 返回
		goBack() {
			uni.showModal({
				title: ' ',
				content: '是否确认退出答题？',
				cancelColor: '#CCCCCC',
				confirmColor: '#03BFB7',
				success: function(res) {
					if (res.confirm) {
						uni.navigateTo({
							url: '../../../wordTasks/index'
						})
						// console.log('用户点击确定');
					} else if (res.cancel) {
						// uni.navigateBack({
						// 	delta: 1
						// })
						// console.log('用户点击取消');
					}
				}
			});
		},
		// 播放音频
		playAudio() {
			const innerAudioContext = uni.createInnerAudioContext();
			innerAudioContext.autoplay = true;
			innerAudioContext.src = 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/audio/music.mp3';
			innerAudioContext.onPlay(() => {
				console.log('开始播放');
			});
			innerAudioContext.onError((res) => {
				console.log(res.errMsg);
				console.log(res.errCode);
			});
		},
		// 下一题
		nextQuestion() {
			clearInterval(this.timer)
			// console.log(this.rightNum)
			// console.log(this.allWordCount)
			if (this.rightNum < this.allWordCount) {
				this.doneCount++
				// console.log("if")
				uni.reLaunch({
					url: "index?index=" + this.doneCount + "&wordIdList=" + this.wordIdList + "&time=" + this.time + '&rightNum=' +
						this.rightNum
				})
			} else {
				// console.log("else")
				clearInterval(this.timer)
				uni.reLaunch({
					url: "passedTask?wordCount=" + this.wordIdList.length + "&time=" + this.time + "&wordId=" + this.wordId
				})
			}
		},

		// 获取题目
		getWordInfo(wordId) {
			this.exampleArr = []
			this.$minApi.getWordInfo({
				wordId: wordId
			}).then(data => {
				// console.log(data)
				this.questionList = data.data
				this.interpretation = data.data.interpretation.split("丶")
				this.example = data.data.example.split("丶")
				this.example.forEach(val => {
					this.exampleArr.push(val.split("&"))
				})
			})
		},
		// 计时器、
		start() {
			this.timer = setInterval(data => {
				this.Second++;
				if (this.Second > 59) {
					this.Second = 0;
					this.Minute++;
					if (this.Minute > 59) {
						this.Minute = 0;
						this.Hours++;
					}
				}
				if (this.Second < 10) {
					this.Secondt = "0" + this.Second;
				} else {
					this.Secondt = this.Second;
				}
				if (this.Minute < 10) {
					this.Minutet = "0" + this.Minute;
				} else {
					this.Minutet = this.Minute;
				}
				if (this.Hours < 10) {
					this.Hourst = "0" + this.Hours;
				} else {
					this.Hourst = this.Hours;
				}
				this.time = this.Hourst + ":" + this.Minutet + ":" + this.Secondt
			}, 1000)
		},

	},

	onLoad(options) {
		
		clearInterval(this.timer)
		this.wordId = options.wordId
		this.doneCount = parseInt(options.doneCount)
		this.wordIdListStr = options.wordIdListStr
		this.wordIdList = options.wordIdListStr.split(",")
		this.time = options.time
		this.getWordInfo(options.wordId)
		this.Hours = parseInt(this.time.split(":")[0])
		this.Minute = parseInt(this.time.split(":")[1])
		this.Second = parseInt(this.time.split(":")[2])
		this.start()
		this.rightNum = options.rightNum
		this.allWordCount=getApp().globalData.wordIdCount
		// console.log(getApp().globalData.wordIdCount)
		this.percentage = ((this.rightNum) / this.allWordCount) * 100
	}

}
